import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import News from "../components/news"
import styles from "./index.module.css"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <div className={styles.main}>
      <h1>PokkeDoll</h1>
      <p>yotsugidoor.moe.hm</p>
    </div>
    <div>
      <div>
        <h2 style={{
          backgroundColor: "#e74c3c",
          color: "white",
          padding: "17px 15px",
          marginTop: "1.45rem"
        }}>お知らせ</h2>
        <News date="2020/05/22" content="メインページを更新しました。"/>
        <News date="2020/02/23" content="DollZⅡのサービスを開始しました。"/>
        <News date="2020/01/01" content="あけましておめでとうございます。今年には公開できるよう頑張ります。"/>
        <News date="2019/10/27" content="サイトを移動。Reactを導入しました。"/>
      </div>
    </div>
  </Layout>
)

export default IndexPage
