import React from "react"
import PropTypes from "prop-types"


const News = ({ date, content }) => (
    <div style={{
        margin: "0 auto",
        padding: "17px 15px",
        backgroundColor: "#f5f5f5",
        color: "black"
    }}>
        <h3>{date}</h3>
        <p>{content}</p>
    </div>
)

News.propTypes = {
    date: PropTypes.string,
    content: PropTypes.string
}

export default News